.sideNav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: fixed;
    background-color: white;
    height: 100vh;
    width: 250px;
    padding: 100px 0 0 0;
    margin: 0 0 0 -200px;
    z-index: 100;
    list-style-type: none;
    -webkit-transition: .2s all ease-in-out;
    -o-transition: .2s all ease-in-out;
    transition: .2s all ease-in-out;
    -webkit-box-shadow: 10px 0px 16px -8px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 0px 16px -8px rgba(0, 0, 0, 0.75);
}

.sideNav li {
    padding-bottom: 40px;
}

.sideNav li  a {
    position: relative;
    overflow: hidden;
    color: #3498db;
    text-decoration: none;
    font-weight: 400;
    font-size: 1.3em;
    padding-left: 40px;
    -webkit-transition: .2s all ease-in;
    -o-transition: .2s all ease-in;
    transition: .2s all ease-in;
}

.sideNav li a:hover {
    color: #B0BEC5;
    margin-left: 15px;
}

.visible {
    margin: 0;
}

.sideNav .material-icons:not(.material-more-options-icon){
    margin-right: 20px;
    vertical-align: bottom;
}

.material-face-icon{
    color: #009688;
}

.material-assignment-icon {
  color: #F44336;
}

.material-email-icon{
  color: #FFEB3B;
}

.material-home-icon{
  color: #673AB7;
}

.material-more-options-icon {
    position: absolute;
    top: 15px;
    right: 10px;
    color: #3498db;
    cursor: pointer;
    -webkit-transition: 0.2s all ease-in;
    -o-transition: 0.2s all ease-in;
    transition: 0.2s all ease-in;
}

.material-more-options-icon:hover {
  color: #B0BEC5;
}


.material-hamburger-icon {
    color: white;
    position: fixed;
    border-radius: 50%;
    top: 10px;
    right: 10px;
    cursor: pointer;
    background-color: #E91E63;
    padding: 10px;
    -webkit-box-shadow: 8px 3px 33px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 8px 3px 33px -1px rgba(0, 0, 0, 0.75);
}

.material-hamburger-icon:after {
    content: '';
    width: 1em;
    height: 1em;
    opacity: 0;
    position: absolute;
    background: rgba(0, 0, 0, .5);
    border-radius: 50%;
    -webkit-animation: menu 2s infinite;
            animation: menu 2s infinite;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
    pointer-events: none;
}

 .nav-icon3{
  width: 36px;
  height: 36px;
  position: absolute;
  top: 10px;
  right: 20px;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: .2s ease-in-out;
  -o-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
  cursor: pointer; 
  padding: 10px;
}

.nav-icon3 span{
  position: absolute;
  background-color: white;
  height: 5px;
  width: 100%;
  border-radius: 0;
  opacity: 1;
  top: 0;
  right: 0;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.nav-icon3 span:nth-child(1) {
  top: 0px;
}

.nav-icon3 span:nth-child(2),.nav-icon3 span:nth-child(3) {
  top: 18px;
}

.nav-icon3 span:nth-child(4) {
  top: 36px;
}

.nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
      transform: rotate(45deg);
}

.nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
}

.nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.material-hamburger-icon.delay-1:after {
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}

.material-hamburger-icon.delay-2:after {
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}

@-webkit-keyframes menu {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    40% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(4);
                transform: scale(4);
        opacity: 0
    }
}

@keyframes menu {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    40% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
    100% {
        -webkit-transform: scale(4);
                transform: scale(4);
        opacity: 0
    }
}

.material-icons.md-36 {
    font-size: 36px;
}


.avatar {
    height: 100vh;
    background-image: url('background.jpg');
    background-color: #3498db;
    background-color: white;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    padding: 0 10%;
    color: white;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.avatar__picture {
    border-radius: 50%;
    padding: 10px;
    border: 2px solid #ccc;
    -webkit-transition: .2s all linear;
    -o-transition: .2s all linear;
    transition: .2s all linear;
}

.avatar__picture:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.avatar__name {
    font-size: 3em;
    margin-top: 0;
    margin-bottom: 0;
}

.avatar__position {
    margin-top: 0;
    margin-bottom: 0;
}

.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 0.9em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.footer__text {
    color: #CFD8DC;
    margin-top: 0;
}

@media screen and (max-width: 600px) {
    .sideNav {
        width: 150px;
        padding: 60px 0 0 0;
        margin: 0 0 0 -120px;
    }

    .sideNav li a {
        padding: 10px 10px;
        font-size: 0.9em;
    }

    .visible {
        margin: 0;
    }

    .material-icons.md-24 {
        font-size: 24px;
    }

    .material-more-options-icon {
        top: 5px;
        right: 5px;
    }

    .avatar{
      padding: 0 10%;
    }

    .avatar__name {
        font-size: 2em;
    }

    .avatar__picture {
        width: 160px;
        height: auto;
    }

    .avatar__text {
        font-size: 0.8em;
    }

    .footer {
        font-size: 0.7em;
    }
}


